/*----------------------  End Utilities  ---------------------- */
* {
  box-sizing: border-box;
}
*:before, *:after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: "Noto Sans", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

main {
  background: #9CCDF5 url("../images/background.jpg") no-repeat center center;
  background-size: cover;
  color: #fff;
  width: 1920px;
  height: 1080px;
  padding: 2em;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.container--grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(13, 1fr);
}

.image__wrapper {
  display: grid;
  grid-template-columns: subgrid;
  grid-template-rows: subgrid;
  grid-area: 1/4/13/9;
}

h1 {
  font: 6.25rem/1.5 "Alfa Slab One", Serif;
  margin: 0 0 0 5rem;
  grid-area: 4/1/12/4;
  text-align: left;
  text-transform: capitalize;
}

.img--1 {
  grid-area: 2/2/6/4;
}
.img--1 img {
  object-position: center;
}
.img--2 {
  grid-area: 4/3/11/5;
}
.img--3 {
  grid-area: 7/1/12/4;
}

.card {
  display: grid;
  grid-row: 12/span 5;
  grid-column: 4/9;
  position: relative;
  top: 3rem;
  right: 5rem;
  width: 100%;
}
.card * + * {
  margin: 0;
}
.card__group {
  display: flex;
  flex-direction: row;
}
.card__image {
  width: 56%;
}
.card__controller .btn {
  border: none;
  background: #111;
  color: #cfcfcf;
  font-size: 0.8rem;
  padding: 1em;
  margin: 0;
}
.card__controller .btn--prev {
  background: #1502AB;
}
.card__controller .btn--next {
  background: #D40404;
}
.card__right {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.card__info {
  background: #F3DFE0;
  color: #06003C;
  margin: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  padding: 0 4.5rem;
}
.card__info a.btn {
  padding: 0.65em 1em;
  font-size: 0.8em;
  border: solid 2px #832525;
  color: #832525;
}
.card__info a.btn:hover {
  background: #832525;
  color: #fff;
}